
import { Component, OnInit, HostListener, HostBinding } from "@angular/core";
import { authService } from './services/authentication.service';
import { HttpModule } from '@angular/http';
import { trigger, state, style, transition, animate} from '@angular/animations';

import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { UserService } from './_services/user.service';
import { ClickService } from './_services/click.service';
import { AuthenticationService } from './_services/authentication.service';
import { SentenceService } from './_services/sentence.service';

@Component({
    moduleId: module.id,
    selector: 'my-app',
    templateUrl: "app.component.html",
    styleUrls: ['./app.component.css'],
    providers: [
        HttpModule,
        BrowserAnimationsModule
    ],
    animations: [
        trigger('slideInOut', [
            state('in', style({
                width: '450px',
                left: "-450px"
            })),
            state('out', style({
                width: '450px',
                left: "0"
            })),
            transition('in => out', animate('400ms ease-in-out')),
            transition('out => in', animate('400ms ease-in-out'))
        ]),
        trigger('archiveMenuSlideInOut', [
            state('in', style({
                transform: 'translate3d(100%, 0, 0)'
            })),
            state('out', style({
                transform: 'translate3d(0,0,0)'
            })),
            transition('in => out', animate('400ms ease-in-out')),
            transition('out => in', animate('400ms ease-in-out'))
        ]),
        trigger('hostAnimation', [
            state('in', style({
                marginLeft: "0px"
            })),
            state('out', style({
                marginLeft: "450px"
            })),
            transition('in => out', animate('400ms ease-in-out')),
            transition('out => in', animate('400ms ease-in-out'))
        ])
    ]
})
export class AppComponent{

    menuState:string = 'in';
    archiveMenuState: string = 'in';
    currentUserName: string = "";
    showNav: boolean;

    @HostBinding('@hostAnimation') random = 'in';

    constructor(
        private userService: UserService,
        private clickService: ClickService,
        private authenticationService: AuthenticationService,
        private sentenceService: SentenceService
    ){
        this.clickService.archiveClick$.subscribe(value => {
            if(value == false){
                this.archiveMenuState = 'in';
            }else{
                this.archiveMenuState = 'out';
            }
        })
        this.authenticationService.isLoggedIn$.subscribe(value => {
            if(value == true){
                console.log("is logged in on true");
                this.showNav = true;
            }else{
                console.log("is logged in on false");
                this.showNav = false;
            }
        })
        this.userService.username$.subscribe(value => {
            this.currentUserName = value;
        })
        this.clickService.bodyClick$.subscribe(value => {
            if(value == true){
                this.random = "out";
            }else{
                this.random = "in";
            }
        })

    }
};
